<template>
	<div class="login">
		<div class="box">
			<p> </p>
			<el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
				<el-form-item label="" prop="phone">
				    <el-input type="phone" v-model="ruleForm2.phone" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="checkPass">
				    <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="age" class="lcode">
				    <el-input v-model.number="ruleForm2.age"></el-input>
				    <label class="getCode" :class="this.class" @click="getCode('ruleForm2')" v-model="ruleForm2.phone">{{message}}</label>
				</el-form-item>
				<el-form-item>
				    <el-button type="primary" @click="submitForm('ruleForm2')"> </el-button>
				    
				</el-form-item>
			</el-form>
			<p>
				<router-link to="register"><span></span></router-link> <span></span>
			</p>
		</div>
	</div>
</template>
<style lang="scss" scoped>
	@import "../style/login.scss"
</style>
<script>
  import * as api from '../api/server.js';
  import {mapState, mapGetters,mapMutations} from 'vuex'
  import axios from 'axios'
  import md5 from 'js-md5'
  export default {
    data() {
      var checkAge = (rule, value, callback) => {
      	const rul = /^\d{6,6}$/;
    		let flag = rul.test(value);
        if (!value) {
          return callback(new Error(''));
        } else if (!flag) {
	          callback(new Error('6!'));
	      } else {
	          callback();
	      }   		
      };
    	var checkphone = (rule,value,callback) => {
    		const rul = /^1((3[0-9]|4[57]|5[0-35-9]|7[035678]|8[0-9])\d{8}$)/;
    		let flag = rul.test(value);
    		if(value===""){
    			callback(new Error(''))
    		}else{
    			callback();
    		}
    	};
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
      	class:"",
      	num:1,
      	message:'',
        ruleForm2: {
          phone: '',
          checkPass: '',
          age: ''
        },
        rules2: {
          phone: [
            { validator: checkphone, trigger: 'blur' }
          ],
          checkPass: [
            { required: true, min: 6, max: 6, message: '6', trigger: 'blur' }
          ],
          age: [
            { validator: checkAge, trigger: 'blur' }
          ]
        }
      };
    },
    mounted(){
      console.log(this.user)
      document.title = this.$route.name;
    },
    created() {
       
    },
    computed:{
      ...mapState(['user']),
    },
    methods: {
      ...mapMutations(['USER_INFO']),
      open(msg){
        this.$alert(msg, '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'info',
              message: `action: ${ action }`
            });
          }
        });
      },
      submitForm(formName) {
      console.log(this.user)
        this.$refs[formName].validate((valid) => {
        var _this = this;
          if (valid) {
            const obj = {};
            obj.account = this.ruleForm2.phone;
            obj.password = md5(this.ruleForm2.checkPass);
            obj.vercode = this.ruleForm2.age.toString();
            console.log(obj)
            api.userLogin(obj)
              .then(function(res){
                if(res.data.result=="success"){
                  _this.setCookies(obj);
                  _this.$router.push({path:'/warning'})
                }else{
                  _this.open(res.data.message)
                }
              })
              .catch(function(err){

              })
           
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      setCookies(obj){
        var day = new Date();
        console.log(obj);
        var str = "";
        day.setTime(day.getTime() + 7*24*60*60*1000);
        for(var key in obj){
          str += key+"="+obj[key]+",";
        }
        str +=  "expires="+day.toGMTString(); 
        document.cookie = str;
        console.log(str)
        console.log(document.cookie)

      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getCode(formName) {
      	  this.$refs[formName].validateField("phone",function(errorMessage){
      	  	console.log(errorMessage)
      	  })

	      	const rul = /^1((3[0-9]|4[57]|5[0-35-9]|7[035678]|8[0-9])\d{8}$)/;
	      	if(rul.test(this.ruleForm2.phone)){
	      		let index = 60;
		        this.class="unclick";
		      	this.message = "("+index+")";
      			var _this = this;
	      		var interval = setInterval(
				     	function(){
				     		if(index>0){
				     			index--;
				     			_this.message = "("+index+")";
				     		}else{
                  clearInterval(interval);
				     			_this.message = "";
			            _this.class = ""
				     		}
				     	},1000);
              let pam = {};
              pam.phone = this.ruleForm2.phone;
              pam.num = this.num;
              api.vercode(pam)
                .then(function(res){
                  if(res.data.result=="success"){
                    _this.open("")
                  }else{
                    _this.open(res.data.message)
                    clearInterval(interval);
                    _this.message = "";
                    _this.class = ""
                  }
                })
                .catch(function(err){
                  _this.open(res.data.message)
                  clearInterval(interval);
                  _this.message = "";
                  _this.class = ""
                })  
	      	}      	
      	}
    	}
  	}
</script>


